.districts {
    padding: 3.5rem 4rem;
    background: #fff;
}

.districts__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
}

.districts__wrapper {
    position: relative;
    margin: 50px 0 0px;
    width: 100%;
}

.districts__items {
    display: flex;
    flex-direction: row;
    margin-bottom: 30px;
    width:100%
}

.districts__item {
    position: relative;
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 8px;
}

.districts__item__link {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 50px;
    color: #252e48;
    box-shadow: 0 6px 20px rgba(2, 29, 36, 0.801);
    border-radius: 10px;
    text-decoration: none;
    align-items: center;
    text-align: center;
    justify-content: center;
    background-color: rgb(248, 246, 246);
}

.districts__item__link:hover {
    box-shadow: 0 6px 20px rgba(65, 6, 6, 0.801);
}

h4 {
  padding: 5% 20% 5% 5%;
  color: #252e48;
  font-size: 18px;
}

.fa-angle-right {
  padding: 5% 5% 5% 0;
}

.districts h1 {
  font-weight: 400;
}

@media screen and (max-width: 1150px) {
    h4 {
      padding: 5% 15% 5% 3%;
    }

    .fa-angle-right {
      padding: 5% 3% 5% 0;
    }
}

@media screen and (max-width: 1024px) {
   .districts__items {
      display: flex;
      flex-direction: column;
      margin-bottom: 0px;
    }

   h4 {
      padding: 5% 30% 5% 5%;
    }

   .fa-angle-right {
      padding: 5% 5% 5% 0;
    }

    .districts__wrapper {
      width: 420px;
    }

    .districts__item {
      margin-bottom: 15px;
    }
}
@media only screen and (max-width: 720px) {
    .districts {
      padding: 0 2.5rem 2.5rem 2.5rem;
    }
}

@media only screen and (max-width: 520px) {
    .districts__wrapper {
      width: 90%;
    }

   h4 {
      padding: 5% 14% 5% 3%;
    }

   .fa-angle-right {
      padding: 5% 3% 5% 0;
    }
}

@media only screen and (max-width: 400px) {
    .districts__wrapper {
      width: 250px;
    }

    h4 {
      padding: 5% 10% 5% 3%;
      font-size: 16px;
    }

    .fa-angle-right {
      padding: 5% 3% 5% 0;
    }
}